.header-green {
    height: 40px;
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: $solid-border;
    padding: 1% 5%;
    img {
        width: 45px;
    }
    ul {
        display: flex;
        justify-content: space-around;
        text-transform: uppercase;
        min-width: 30%;
        li {
            padding-right: 10px;
        }
    }
}

.header-white {
    @extend .header-green;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    border-bottom: none;
    a {
        color: white;
        &:hover {
            color: $orange;
        }
    }
   
}